import * as moment from 'moment';
import {Calendar} from "@ionic-native/calendar";
import {CalendarProvider} from "../../../providers/calendar/calendarProvider";
import {CalendarEvent} from "../../../providers/calendar/calendarEvent.Model";
import {StorageManagerProvider} from "../../../providers/storage-manager/storage-manager";
import {Storage} from "@ionic/storage";
import {ApiProvider} from "../../../providers/api/api";
import {registerLocaleData} from "@angular/common";
import localeDe from "@angular/common/locales/de"
import {TherapyEntryPage} from "../../therapy-entry/therapy-entry";
import {HomePage} from "../../home/home";
